<template>
  <div class="back">
    <app-bar
      back-icon
      title="发布历史"
      hide-user-menu
      hideMessage
      hideDemand
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)" class="mt-8 mb-8">
      <v-container>
        <v-card class="mx-auto" min-width="800">
          <div class="ml-5 mr-5">
            <el-table style="width: 100%" stripe :data="filterTable">
              <el-table-column
                label="项目名称"
                prop="entryname"
              ></el-table-column>
              <el-table-column label="创建者" prop="uploads"></el-table-column>
              <el-table-column label="创建时间" prop="time"></el-table-column>
              <el-table-column label="状态" prop="status"></el-table-column>
              <el-table-column>
                <!-- eslint-disable-next-line -->
                <template slot="header" slot-scope="scope">
                  <el-input
                    v-model="search"
                    size="mini"
                    placeholder="输入项目名称以搜索"
                  ></el-input>
                </template>
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.row.id, scope.row.status)"
                    >编辑</el-button
                  >
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row.id)"
                    >删除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'PersonalCenter',
  components: {
    AppBar,
    AppFooter
  },
  data() {
    return {
      tableData: [],
      search: ''
    }
  },
  computed: {
    filterTable() {
      if (this.tableData) {
        let fTable = this.tableData.filter(
          p => p.entryname.indexOf(this.search) !== -1
        )
        //排序
        fTable.sort(
          (a, b) => new Date(b.time).getTime() - new Date(a.time).getTime()
        )
        return fTable
      } else {
        return []
      }
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    handleEdit(id) {
      this.$router.push('/university/history/detail/' + id)
    },
    async handleDelete(id, status) {
      if (status == '已审核') {
        let res = await this.$http.get('/api/school/deleteSci' + id)
        if (res.data.code != 200) {
          this.$message.error('删除科研成果失败！')
          return false
        } else {
          this.$message.success('删除科研成果成功！')
        }
      } else {
        this.$message.warning('无法删除正在审核的科研成果，请联系管理员')
      }
    },
    async getList() {
      let res = await this.$http.get('/api/school/readMydraft')
      if (res.data.code === 200) {
        this.tableData = res.data.data.Sci
      } else {
        this.$message.error('暂无法获取科研成果发布历史')
        return false
      }
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
